import { Flex, SimpleGrid, Spinner, Stack } from "@chakra-ui/react";
import { lazy, FC, Suspense, useEffect } from "react";
import { useDashboardStyles } from "../Dashboard/styles";
import { DashExploreIcon, DashIcon1, DashShopIcon } from "../../../assets/iconsSvg/iconsSvg";
import Images_Icons from "../../../constants/icons-images";
import { useSelector } from "react-redux";
import { AuthReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import Container from "../../../components/Container";
import { useGetOffersBasedOnMyPointsQuery } from "../../../redux/rtkQuery/api";
import { nanoid } from "@reduxjs/toolkit";
import DashboardProductItem from "../../../components/DashboardProductItem";
import SkeletonLoader from "../../../components/SkeletonLoader";
import { useNavigate } from "react-router-dom";
import DashboardProductItemOld from "../../../components/DashboardProductItemOld";
import { GLOBAL_PREFIX } from "../../../constants";

const Footer = lazy(() => import("../../../components/Footer"));
const Breadcrumbs = lazy(() => import("../../../components/Breadcrumbs"));
const DashboardTopCard = lazy(() => import("../../../components/DashboardTopCard"));
// const DashboardNavBar = lazy(
//   () => import("../../../components/DashboardNavBar")
// );
const NewDashboardNavBar = lazy(() => import("../../../components/NewDashboardNavBar"));
const DashboardNewTopCard = lazy(() => import("../../../components/DashboardNewTopCard"));

const DashboardOffers: FC = (): JSX.Element => {
  let navigate = useNavigate();
  const { root } = useDashboardStyles();

  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);

  const { data, isLoading, error }: any = useGetOffersBasedOnMyPointsQuery(
    { page: 1, limit: 7 },
    { refetchOnMountOrArgChange: 300 }
  );

  // useEffect(() => {
  //   console.log(data, "LLKIOIOI");
  // }, []);

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      <Suspense fallback={<Spinner size="sm" />}>
        {/* <DashboardNavBar borderBottomRightRadius={"0px"} /> */}
        <NewDashboardNavBar />
      </Suspense>
      {/* ----------------------------------------------------------------------TOP CARD------------------------------------------------------------ */}
      <Container>
        <Stack {...root}>
          <Suspense fallback={<Spinner size="sm" />}>
            <Container>
              <Breadcrumbs
                breadcrumbs={[
                  {
                    name: "Home",
                    link: token ? `${GLOBAL_PREFIX}/dashboard` : `${GLOBAL_PREFIX}/`,
                  },
                  {
                    name: "Offers",
                    link: `${GLOBAL_PREFIX}/dashboard/offers`,
                    isCurrentPage: true,
                  },
                ]}
              />
            </Container>

            <DashboardNewTopCard
              title={`Discover worlds.`}
              desc={"Explore brand’s shops."}
              buttonLabel={""}
              icon={<DashExploreIcon color={"#FFFFFF"} w={"330px"} h={"300px"} />}
              design={""}
              // design={Images_Icons.dashboardCardDesign2}
              onClick={() => {
                navigate(`${GLOBAL_PREFIX}/dashboard/shop`);

                // navigate(
                //   `/dashboard/category/${
                //     data?.data?.[Math.floor(Math.random() * 7)].name
                //   }`,
                //   { state: data?.data?.[Math.floor(Math.random() * 7)] }
                // );
              }}
            />
          </Suspense>

          {isLoading ? (
            <SkeletonLoader />
          ) : (
            <SimpleGrid
              columns={[2, 2, 3, 4, 4]}
              w={"100%"}
              spacingX={["35px", "35px", "35px", "15px", "35px"]}
              spacingY={"5px"}
              // justifyContent="space-between"
              // justifyItems={"space-between"}
            >
              <Suspense fallback={<Spinner size="sm" />}>
                {data?.data?.offers?.map(
                  (
                    e: {
                      id: number;
                      slug: string;

                      name: string;
                      category: { name: string };
                      price: number | string;
                      point_discount: number | string;
                      isNew: boolean;
                      images: Array<string>;
                      reward: {
                        rewardSymbol: string;
                      };
                    },
                    i: number
                  ) => (
                    <DashboardProductItem
                      key={nanoid()}
                      isLoaded={!isLoading}
                      productTitle={e.name}
                      points={`${e?.point_discount}${e.reward?.rewardSymbol}`}
                      productSubtitle={e.category.name}
                      price={e.price}
                      isNew={e.isNew}
                      // trending={e.trending}
                      bgImage={[]}
                      rest={{ ...e }}
                      i={i}
                      onClick={() => navigate(`${GLOBAL_PREFIX}/productDetails/${e.slug}`)}
                    />
                  )
                )}
              </Suspense>
            </SimpleGrid>
          )}
        </Stack>
      </Container>

      <Suspense fallback={<Spinner size="sm" />}>
        <Footer />
      </Suspense>
    </Flex>
  );
};

export default DashboardOffers;
